import React, { useState } from "react";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import { v4 as uuid } from "uuid";
import Plot from "react-plotly.js";
import Button from "@mui/material/Button";

const itemsFromBackend = [
  {
    id: uuid(),
    content: {
      type: "pie",
      values: [2, 3, 4, 4],
      labels: ["Wages", "Operating expenses", "Cost of sales", "Insurance"],
      textinfo: "label+percent",
      textposition: "outside",
      automargin: true,
    },
  },
  {
    id: uuid(),
    content: {
      type: "pie",
      values: [2, 3, 4, 4],
      labels: ["Wages", "Operating expenses", "Cost of sales", "Insurance"],
      textinfo: "label+percent",
      textposition: "outside",
      automargin: true,
    },
  },
];

const columnsFromBackend = {
  [uuid()]: {
    name: "Modèles",
    items: itemsFromBackend,
  },
  [uuid()]: {
    name: "Entreprise 1",
    items: [],
  },
  [uuid()]: {
    name: "Entreprise 2",
    items: [],
  },
  [uuid()]: {
    name: "Entreprise 3",
    items: [],
  },
};

const onDragEnd = (result, columns, setColumns) => {
  if (!result.destination) return;
  const { source, destination } = result;

  if (source.droppableId !== destination.droppableId) {
    const sourceColumn = columns[source.droppableId];
    const destColumn = columns[destination.droppableId];
    const sourceItems = [...sourceColumn.items];
    const destItems = [...destColumn.items];
    const [removed] = sourceItems.splice(source.index, 1);
    destItems.splice(destination.index, 0, removed);
    setColumns({
      ...columns,
      [source.droppableId]: {
        ...sourceColumn,
        items: sourceItems,
      },
      [destination.droppableId]: {
        ...destColumn,
        items: destItems,
      },
    });
  } else {
    const column = columns[source.droppableId];
    const copiedItems = [...column.items];
    const [removed] = copiedItems.splice(source.index, 1);
    copiedItems.splice(destination.index, 0, removed);
    setColumns({
      ...columns,
      [source.droppableId]: {
        ...column,
        items: copiedItems,
      },
    });
  }
};

function DragDrop() {
  const [columns, setColumns] = useState(columnsFromBackend);
  const [modelsVisible, setModelsVisible] = useState(false);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        height: "100%",
        margin: "20px",
      }}
    >
      <div>
        <Button
          sx={{ p: 3, height: 5 }}
          variant="contained"
          color="primary"
          onClick={() => setModelsVisible(!modelsVisible)}
        >
          {modelsVisible ? "Masquer les modèles" : "Afficher les modèles"}
        </Button>
      </div>
      <DragDropContext
        onDragEnd={(result) => onDragEnd(result, columns, setColumns)}
      >
        {Object.entries(columns).map(([columnId, column], index) => {
          {
            if (index === 0 && !modelsVisible) {
              return null;
            }
          }
          return (
            <div
              style={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
              }}
              key={columnId}
            >
              <h2>{column.name}</h2>
              <div style={{ margin: 8 }}>
                <Droppable droppableId={columnId} key={columnId}>
                  {(provided, snapshot) => {
                    return (
                      <div
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                        style={{
                          background: snapshot.isDraggingOver
                            ? "lightblue"
                            : "lightgrey",
                          padding: 4,
                          width: 250,
                          minHeight: 500,
                        }}
                      >
                        {column.items.map((item, index) => {
                          return (
                            <Draggable
                              key={item.id}
                              draggableId={item.id}
                              index={index}
                            >
                              {(provided, snapshot) => {
                                return (
                                  <div
                                    ref={provided.innerRef}
                                    {...provided.draggableProps}
                                    {...provided.dragHandleProps}
                                    style={{
                                      userSelect: "none",
                                      padding: 16,
                                      margin: "0 0 8px 0",
                                      minHeight: "50px",
                                      backgroundColor: snapshot.isDragging
                                        ? "#263B4A"
                                        : "#456C86",
                                      color: "white",
                                      ...provided.draggableProps.style,
                                    }}
                                  >
                                    <Button
                                      variant="contained"
                                      color="error"
                                      sx={{ p: 1.5, ml: 19, height: 3 }}
                                      onClick={() => {
                                        const newItems = [
                                          ...column.items.slice(0, index),
                                          ...column.items.slice(index + 1),
                                        ];
                                        setColumns({
                                          ...columns,
                                          [columnId]: {
                                            ...column,
                                            items: newItems,
                                          },
                                        });
                                      }}
                                    >
                                      X
                                    </Button>
                                    <Plot
                                      data={[
                                        {
                                          type: item.content.type,
                                          values: item.content.values,
                                          labels: item.content.labels,
                                          textinfo: item.content.textinfo,
                                          textposition: item.content.textinfo,
                                          automargin: item.content.automargin,
                                        },
                                      ]}
                                      layout={{
                                        height: 230,
                                        width: 220,
                                        margin: { t: 0, b: 0, l: 0, r: 0 },
                                        showlegend: false,
                                      }}
                                    />
                                  </div>
                                );
                              }}
                            </Draggable>
                          );
                        })}
                        {provided.placeholder}
                      </div>
                    );
                  }}
                </Droppable>
              </div>
            </div>
          );
        })}
      </DragDropContext>
      <Button
        variant="contained"
        color="success"
        sx={{ p: 3, ml: 18, height: 5 }}
        onClick={() => {
          const newItems = [
            ...columns[Object.keys(columns)[0]].items,
            {
              id: uuid(),
              content: {
                type: "pie",
                values: [2, 3, 4, 4],
                labels: [
                  "Wages",
                  "Operating expenses",
                  "Cost of sales",
                  "Insurance",
                ],
                textinfo: "label+percent",
                textposition: "outside",
                automargin: true,
              },
            },
          ];
          setColumns({
            ...columns,
            [Object.keys(columns)[0]]: {
              ...columns[Object.keys(columns)[0]],
              items: newItems,
            },
          });
          setModelsVisible(true);
        }}
      >
        Ajouter model
      </Button>
    </div>
  );
}

export default DragDrop;
